@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Pinstripe:ital@0;1&family=Poppins:wght@300;500;800&display=swap');

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
}

:root {
    /* Color Palette */
    --bg-color: #F2F3F4;
    --accent-color: #FFF44E;
    --primary-color: #5FAAE3;
    --dark-color: #246BCF;
    --light-color: #9cc9ec;
    --border-color: grey;

    /* Break points */
    --width-lg: 80%;
    --width-md: 90%;
    --width-sm: 100%;
    /* Animations */
    --default-transition: all 300 ease;

    --shadow: inset 1px 2px 20px;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Popins', sans-serif;
    line-height: 1.6;
    background-color: var(--bg-color);
}

/* General Styles */
.container {
    width: var(--width-lg);
    height: 100vh;
    margin: 0 auto;
    padding: auto 0;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

img {
    display: inline-block;
}

a {
    
    color:var(--dark-color);
}
a:hover{
    color: var(--light-color);
}
.cta{
    background-color: var(--accent-color);
    color: var(--primary-color);
}

/* Media queries */
@media only screen and (max-width: 600px) {
    .container {
        width: var(--width-sm);

    }
}

@media only screen and (min-width: 601px) {
    .container {
        width: var(--width-md);
    }
}

@media only screen and (min-width: 1023px) {
    .container {
        width: var(--width-lg);
    }
}